
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.demo-item {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  vertical-align: top;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 1px 0px rgba(0,0,0,0.36);
}
.demo-item-container {
  padding: 20px;
}
.demo-description {
  position: relative;
  font-size: 14px;
  color: #666;
  padding: 10px 20px;
}
.demo-description .hljs p {
  margin-bottom: 5px;
}
.demo-item-title {
  position: relative;
  font-size: 14px;
  color: #666;
  height: 20px;
  line-height: 20px;
  padding-left: 30px;
  background-color: #fff;
}
.demo-item-title::before {
  content: attr(data-title);
  position: absolute;
  left: 30px;
  top: 0;
  height: 100%;
  padding: 0 10px;
  font-size: 16px;
  font-weight: 500;
  background-color: #fff;
  z-index: 2;
}
.demo-item-title::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #efefef;
  z-index: 1;
}
.show-code-btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  display: inline-block;
  font-size: 14px;
  padding: 0;
  cursor: pointer;
  transition: all 0.2s;
}
.show-code-btn.open {
  transform: rotate(180deg);
  color: #00a0e9;
}
.slide-enter-active,
.slide-leave-active {
  max-height: 300px;
  overflow: hidden;
  transition: all 0.3s ease;
}
.slide-enter,
.slide-leave-to {
  max-height: 0;
}


