.demo-container {
  position: relative;
  width: 100%;
  height: 100%;
  zoom: 1;
}
.demo-container:before,
.demo-container:after {
  content: "";
  display: table;
}
.demo-container:after {
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}


