
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-loading {
  position: fixed;
  top: 0;
  left: 0;
  height: 2px;
  transition: all 0.2s linear;
  z-index: 999;
}
.hiui-loading.hiui-loading-loading {
  background-color: #00a0e9;
}
.hiui-loading.hiui-loading-success {
  background-color: #4fae9b;
}
.hiui-loading.hiui-loading-error {
  background-color: #f02f45;
}
.hiui-loading .hiui-icon {
  position: fixed;
  top: 5px;
  right: 5px;
  color: #00a0e9;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}


