
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-radio {
  cursor: pointer;
  display: inline-block;
  position: relative;
  user-select: none;
}
.hiui-radio .radio-icon {
  width: 15px;
  height: 15px;
  transition: border-color 0.3s;
  position: relative;
  display: inline-block;
  border-radius: 50%;
  border: 1px solid #ccc;
  vertical-align: -2px;
}
.hiui-radio .radio-icon .active-sign {
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  background: #f02f45;
  border-radius: 50%;
}
.hiui-radio .radio-label {
  display: inline-block;
  cursor: pointer;
}
.hiui-radio .hidden-input {
  opacity: 0;
  position: absolute;
  cursor: pointer;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hiui-radio:not(.disabled):hover .radio-icon {
  border-color: #999;
}
.hiui-radio.active .radio-icon {
  border-color: #999;
}
.hiui-radio.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
.hiui-radio.disabled * {
  cursor: not-allowed;
}
.hiui-radio-sm {
  font-size: 12px;
}
.hiui-radio-sm .radio-icon {
  width: 13px;
  height: 13px;
}
.hiui-radio-md {
  font-size: 14px;
}
.hiui-radio-md .radio-icon {
  width: 15px;
  height: 15px;
}
.hiui-radio-lg {
  font-size: 16px;
}
.hiui-radio-lg .radio-icon {
  width: 17px;
  height: 17px;
}
.bounce-enter-active {
  animation: bounce-in 0.3s;
}
.bounce-leave-active {
  animation: bounce-out 0.1s;
}
@-moz-keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@-o-keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@-moz-keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}
@-webkit-keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}
@-o-keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}
@keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}


