
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-tabBar {
  position: relative;
  height: 40px;
  line-height: 40px;
  margin-bottom: 15px;
  border-bottom: 1px solid #e0e0e0;
  white-space: nowrap;
  box-sizing: content-box;
}
.hiui-tabBar .hiui-tabBar-item {
  position: relative;
  height: 40px;
  line-height: 40px;
  display: inline-block;
  text-align: center;
  font-size: 14px;
  color: #666;
  cursor: pointer;
}
.hiui-tabBar .hiui-tabBar-item.active {
  color: #00a0e9;
  border-bottom: 2px solid #00a0e9;
}
.hiui-tabBar .hiui-tabBar-item .hiui-icon {
  font-size: 16px;
}
.hiui-tabBar .hiui-tabBar-item .hiui-icon-prefix {
  margin-right: 6px;
}
.hiui-tabBar .hiui-tabBar-item .hiui-icon-suffix {
  margin-left: 6px;
}
.hiui-tabBar-padding .hiui-tabBar-item {
  padding: 0 15px;
}
.hiui-tabBar-no_padding .hiui-tabBar-item {
  padding: 0;
  margin-right: 30px;
}
.hiui-tabBar-ticket {
  border-bottom: 0;
}
.hiui-tabBar-ticket .hiui-tabBar-item {
  color: #cc4454;
  background-color: #f5ddac;
  border-left: 1px dashed #fff;
}
.hiui-tabBar-ticket .hiui-tabBar-item:first-child,
.hiui-tabBar-ticket .hiui-tabBar-item.active,
.hiui-tabBar-ticket .hiui-tabBar-item.active+.hiui-tabBar-item {
  border-left-width: 0;
}
.hiui-tabBar-ticket .hiui-tabBar-item.active {
  color: #fff;
  background-color: #cc4454;
  border-bottom: none;
}
.hiui-tabBar-ticket .hiui-tabBar-item.active::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 10px 0 10px;
  border-color: #cc4454 transparent transparent transparent;
}
.hiui-tabBar-card .hiui-tabBar-item {
  margin-right: 4px;
  background-color: #f5f7f9;
  border: 1px solid #e0e0e0;
  border-bottom: 0;
  border-radius: 4px 4px 0 0;
}
.hiui-tabBar-card .hiui-tabBar-item.active {
  color: #00a0e9;
  background-color: #fff;
  height: 41px;
  border-bottom: 0;
  transform: translateZ(0);
}


