
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #f5f5f5;
}
.main {
  display: flex;
  flex: 1;
  margin: 40px 80px;
  border-radius: 8px;
  border: 1px solid #efefef;
  overflow: hidden;
}
.left-content {
  display: flex;
  width: 250px;
  background-color: #fff;
  border-right: 1px solid #ccc;
}
.right-content {
  flex: 1;
  padding: 10px 30px;
  background-color: #fff;
  overflow: hidden;
}
.right-content .markdown-body {
  color: rgba(0,0,0,0.7);
}
.right-content .markdown-body table code {
  color: #0070a3;
}


