
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-toast {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 8px 15px;
  background-color: #666;
  border-radius: 4px;
  color: #fff;
}
.hiui-toast.hiui-toast-default {
  background-color: #666;
}
.hiui-toast.hiui-toast-primary {
  background-color: #00a0e9;
}
.hiui-toast.hiui-toast-error {
  background-color: #f02f45;
}
.hiui-toast.hiui-toast-warning {
  background-color: #fac450;
}
.hiui-toast.hiui-toast-success {
  background-color: #4fae9b;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}


