
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-btn {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  white-space: nowrap;
  border-radius: 4px;
  color: #666;
  vertical-align: middle;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  user-select: none;
  position: relative;
  color: rgba(0,0,0,0.65);
  outline: none;
  box-shadow: none;
  cursor: pointer;
  transition: all 0.2s;
}
.hiui-btn:hover {
  border-color: #989898;
}
.hiui-btn:active {
  box-shadow: inset 0 2px 2px rgba(0,0,0,0.12);
}
.hiui-btn.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
.hiui-btn.disabled:active {
  box-shadow: none;
}
.hiui-btn-sm {
  padding: 0 12px;
  font-size: 12px;
  height: 24px;
}
.hiui-btn-sm .hiui-icon {
  margin: 0 -1px;
  font-size: 14px;
}
.hiui-btn-md {
  padding: 0 14px;
  font-size: 14px;
  height: 34px;
}
.hiui-btn-md .hiui-icon {
  margin: 0 -1px;
  font-size: 16px;
}
.hiui-btn-lg {
  padding: 0 16px;
  font-size: 16px;
  height: 44px;
}
.hiui-btn-lg .hiui-icon {
  margin: 0 -1px;
  font-size: 18px;
}


