.hiui-tabs {
  position: relative;
}
.hiui-tabs-bar {
  position: relative;
  margin-bottom: 15px;
  border-bottom: 1px solid #e0e0e0;
  white-space: nowrap;
}
.hiui-tabs-bar-over {
  padding: 0 12px;
}
.hiui-tabs-bar-scroll {
  float: left;
  transition: transform 0.3s;
}
.hiui-tabs-bar-nav {
  width: 100%;
  overflow: hidden;
}
.hiui-tabs-bar-prev,
.hiui-tabs-bar-next {
  position: absolute;
  top: 0;
  height: 100%;
  line-height: 40px;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
}
.hiui-tabs-bar-prev.disabled,
.hiui-tabs-bar-next.disabled {
  opacity: 0.2;
  cursor: not-allowed;
}
.hiui-tabs-bar-prev {
  left: 0;
}
.hiui-tabs-bar-next {
  right: 0;
}
.hiui-tabs-bar-item {
  position: relative;
  height: 40px;
  line-height: 40px;
  display: inline-block;
  text-align: center;
  font-size: 14px;
  color: #666;
  cursor: pointer;
}
.hiui-tabs-bar-item-padding {
  padding: 0 15px;
}
.hiui-tabs-bar-item-no_padding {
  padding: 0;
}
.hiui-tabs-bar-item-no_padding:not(:last-child) {
  margin-right: 30px;
}
.hiui-tabs-bar-item-fix_width {
  padding: 0;
}
.hiui-tabs-bar-item:hover {
  color: #333;
}
.hiui-tabs-bar-item.active {
  color: #00a0e9;
  border-bottom: 2px solid #00a0e9;
}
.hiui-tabs-bar-item.disabled {
  opacity: 0.2;
  cursor: not-allowed;
}
.hiui-tabs-bar-item .hiui-icon {
  font-size: 16px;
  margin-right: 6px;
}


