
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-checkbox {
  display: inline-block;
  position: relative;
  user-select: none;
}
.hiui-checkbox.disabled {
  opacity: 0.3;
}
.hiui-checkbox.disabled .hidden-input {
  cursor: not-allowed;
}
.hiui-checkbox.active .box {
  border-color: #999;
}
.hiui-checkbox:not(.disabled):hover .box {
  border-color: #999;
}
.hiui-checkbox .box {
  border: 1px solid #ccc;
  width: 15px;
  height: 15px;
  padding: 2px;
  display: inline-block;
  transition: border-color 0.3s;
  vertical-align: text-bottom;
}
.hiui-checkbox .box .item {
  width: 100%;
  height: 100%;
  background-color: #f02f45;
  display: block;
}
.hiui-checkbox .checkbox-label {
  display: inline-block;
}
.hiui-checkbox .hidden-input {
  opacity: 0;
  position: absolute;
  cursor: pointer;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.hiui-checkbox-sm {
  font-size: 12px;
}
.hiui-checkbox-sm .box {
  width: 13px;
  height: 13px;
}
.hiui-checkbox-md {
  font-size: 14px;
}
.hiui-checkbox-md .box {
  width: 15px;
  height: 15px;
}
.hiui-checkbox-lg {
  font-size: 16px;
}
.hiui-checkbox-lg .box {
  width: 17px;
  height: 17px;
}
.bounce-enter-active {
  animation: bounce-in 0.3s;
}
.bounce-leave-active {
  animation: bounce-out 0.1s;
}
@-moz-keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@-o-keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@keyframes bounce-in {
0% {
    transform: scale(0.5);
    opacity: 0;
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@-moz-keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}
@-webkit-keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}
@-o-keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}
@keyframes bounce-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.5);
    opacity: 0;
}
}


