
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-spinner {
  color: #666;
  vertical-align: middle;
  text-align: center;
}
.hiui-spinner.hiui-spinner-wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #00a0e9;
  background-color: rgba(0,0,0,0.8);
  z-index: 99;
}
.hiui-spinner.hiui-spinner-wrap .hiui-spinner-content {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hiui-spinner.hiui-spinner-wrap .hiui-icon {
  display: block;
}


