
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-grid-row {
  display: block;
  position: relative;
  margin-left: 0;
  margin-right: 0;
  zoom: 1;
}
.hiui-grid-row:before,
.hiui-grid-row:after {
  content: "";
  display: table;
}
.hiui-grid-row:after {
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}


