
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-input {
  vertical-align: top;
  position: relative;
  display: inline-block;
  background-color: #fff;
  border-radius: 2px;
  height: 44px;
  transition: border-color 0.3s;
  transition: border-color 0.3s, box-shadow 0.3s;
  cursor: text;
}
.hiui-input.textarea {
  height: auto !important;
}
.hiui-input.with-border {
  border: 1px solid #ccc;
}
.hiui-input:not(.textarea) {
  padding: 0 14px;
}
.hiui-input:not(.disabled):hover {
  border-color: #999;
}
.hiui-input.disabled {
  cursor: not-allowed;
  opacity: 0.3;
}
.hiui-input.disabled * {
  cursor: not-allowed;
}
.hiui-input.with-border.focus {
  border-color: #999;
  box-shadow: 0 0 0 2px rgba(153,153,153,0.2);
}
.hiui-input.animated .hiui-input-label {
  margin-top: -8px;
  transform: translateY(-50%) scale(0.8);
}
.hiui-input.animated .hiui-input-field {
  margin-top: 8px;
}
.hiui-input.error {
  border-color: #f02f45;
}
.hiui-input.error:hover {
  border-color: #f02f45 !important;
}
.hiui-input-sm {
  width: 140px;
  height: 38px;
  font-size: 12px;
}
.hiui-input-md {
  width: 230px;
  height: 44px;
  font-size: 14px;
}
.hiui-input-lg {
  width: 320px;
  height: 50px;
  font-size: 16px;
}
.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}


