
@import '//at.alicdn.com/t/font_bwko6yrbca6ecdi.css';
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-icon {
  position: relative;
  display: inline-block;
  vertical-align: baseline;
  font-size: 24px;
}
.hiui-icon svg {
  display: block;
}
.hiui-icon-rotate {
  animation: rotate 1s infinite linear;
}
@-moz-keyframes rotate {
form {
    transform: rotate(0);
}
to {
    transform: rotate(360deg);
}
}
@-webkit-keyframes rotate {
form {
    transform: rotate(0);
}
to {
    transform: rotate(360deg);
}
}
@-o-keyframes rotate {
form {
    transform: rotate(0);
}
to {
    transform: rotate(360deg);
}
}
@keyframes rotate {
form {
    transform: rotate(0);
}
to {
    transform: rotate(360deg);
}
}


