
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-input-tip {
  color: #f02f45;
  font-size: 12px;
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0;
  height: 100%;
  background-color: rgba(255,255,255,0.9);
}
.hiui-input-tip.middle:after {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.hiui-input-tip.top .tip-wrapper {
  margin-top: 5px;
  margin-right: 5px;
}
.hiui-input-tip .tip-wrapper {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  background-color: #fff;
}
.hiui-input-tip .tip-wrapper .tip {
  margin: 0;
  display: inline-block;
  white-space: nowrap;
}
.hiui-input-tip .tip-wrapper .correct-sign {
  position: relative;
  vertical-align: middle;
  text-align: center;
  border: 1px solid #4fae9b;
  display: inline-block;
  border-radius: 100%;
  width: 18px;
  height: 18px;
}
.hiui-input-tip .tip-wrapper .correct-sign .icon {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.slide-up-enter-active,
.slide-up-leave-active {
  transition: opacity 0.3s, transform 0.3s;
}
.slide-up-enter,
.slide-up-leave-active {
  opacity: 0;
  transform: translateY(15px);
}


