.hiui-select-item {
  position: relative;
  padding: 10px 16px;
  background-color: #fff;
  cursor: pointer;
  white-space: nowrap;
}
.hiui-select-item.active {
  color: #f02f45;
}
.hiui-select-item:hover {
  background-color: #f5f5f5;
}
.hiui-select-item + .hiui-select-item {
  border-top: 1px solid #eee;
}


