.hiui-input-field {
  color: #323232;
  display: block;
  width: 100%;
}
.hiui-input-field:not(.textarea) {
  position: absolute;
  z-index: 8;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: margin-top 0.3s;
}
.hiui-input-field.no-padding textarea {
  padding: 0;
}
.hiui-input-field input {
  line-height: 1;
  display: block;
  width: 100%;
  height: 100%;
  -webkit-appearance: textfield;
  border: 0;
  outline: none;
  background: transparent;
  color: inherit;
}
.hiui-input-field input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
.hiui-input-field input::placeholder {
  color: #999;
}
.hiui-input-field textarea {
  resize: vertical;
  padding: 10px 14px;
  display: block;
  width: 100%;
  -webkit-appearance: textfield;
  border: 0;
  outline: none;
  background: transparent;
  color: inherit;
}
.hiui-input-field textarea.hidden-textarea {
  opacity: 0;
  position: absolute;
  z-index: -1;
}


