
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.nav {
  width: 100%;
  padding: 20px;
}
.nav .nav-li small {
  font-size: 12px;
  margin-left: 4px;
  opacity: 0.85;
}
.nav .nav-child-wrap {
  margin-left: 30px;
}
.nav .nav-child-wrap .nav-child {
  padding: 8px 15px;
}
.nav .nav-child-wrap .nav-child:hover {
  color: #0070a3;
  cursor: po;
}
.nav .nav-child-wrap .nav-child-title {
  font-size: 14px;
  color: #999;
  margin: 5px 0;
}
.link-li {
  position: relative;
  font-size: 16px;
  color: #666;
  padding: 12px 10px;
}
.link-li:hover {
  color: #0070a3;
}
.link-li.router-link-active {
  color: #00a0e9;
  cursor: default;
}
.link-li.router-link-active::before {
  content: '';
  position: absolute;
  color: #00a0e9;
  width: 0;
  height: 0;
  left: -2px;
  top: 50%;
  font-size: 12px;
  transform: translateY(-50%);
  border: 6px solid transparent;
  border-left-color: #00a0e9;
}
.link-li:not(.router-link-active) {
  cursor: pointer;
}


