
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
  size_lg: 44px;
}
.hiui-select {
  cursor: pointer;
  transition: border-color 0.3s, box-shadow 0.3s;
  position: relative;
  height: 44px;
  width: 230px;
  padding: 0 15px 0 10px;
  border-radius: 2px;
  border: 1px solid #ccc;
  display: inline-block;
}
.hiui-select.hover {
  border-color: #999;
}
.hiui-select.hover .handler .arrow {
  border-color: #999;
}
.hiui-select.error {
  border-color: #f56d7d;
}
.hiui-select.error .handler .arrow {
  border-color: #f56d7d;
}
.hiui-select.error:not(.active).hover {
  border-color: #f56d7d !important;
}
.hiui-select.error:not(.active).hover .handler .arrow {
  border-color: #f56d7d !important;
}
.hiui-select.disabled {
  border-color: #e0e0e0;
}
.hiui-select.disabled .handler .arrow {
  border-color: #ccc;
}
.hiui-select.active {
  border-color: #999;
  box-shadow: 0 0 0 2px rgba(153,153,153,0.2);
}
.hiui-select.active .handler .arrow {
  border-color: #999;
  transform: translateY(-50%) rotate(-135deg);
}
.hiui-select.has-icon .handler .selected {
  padding-left: 30px;
}
.hiui-select.has-icon .handler .placeholder {
  padding-left: 30px;
}
.hiui-select .handler {
  width: 100%;
  height: 100%;
  position: relative;
}
.hiui-select .handler .icon {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.hiui-select .handler .label {
  transform-origin: left top;
  line-height: 1;
  transition: transform 0.3s, margin-top 0.3s;
  color: #999;
  position: absolute;
  z-index: 1;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-bottom: 0;
}
.hiui-select .handler .label.float {
  transform: translateY(-50%) scale(0.8);
  margin-top: -8px;
}
.hiui-select .handler .selected {
  max-width: 90%;
  line-height: 1;
  position: absolute;
  z-index: 1;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: margin-top 0.3s;
  display: block;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.hiui-select .handler .selected.float {
  margin-top: 8px;
}
.hiui-select .handler .arrow {
  transition: transform 0.3s, border-color 0.3s;
  border: 1px solid #ccc;
  border-width: 0 1px 1px 0;
  width: 8px;
  height: 8px;
  position: absolute;
  z-index: 1;
  right: 0;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
}
.hiui-select .body {
  box-shadow: 0 0 5px 1px rgba(0,0,0,0.2);
  min-width: 100%;
  position: absolute;
  background-color: #fff;
  z-index: 99;
  right: 0;
  top: 100%;
  margin-top: 6px;
  max-height: 350px;
  overflow: auto;
  border-radius: 2px;
}
.hiui-select-sm {
  font-size: 12px;
  height: 38px;
  width: 140px;
}
.hiui-select-md {
  font-size: 14px;
  height: 44px;
  width: 230px;
}
.hiui-select-lg {
  font-size: 16px;
  height: 50px;
  width: 320px;
}
.tab-hack {
  position: absolute;
  opacity: 0;
}
.disabled-mask {
  position: absolute;
  z-index: 9;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(255,255,255,0.6);
  cursor: not-allowed;
}
.slide-down-enter-active,
.slide-down-leave-active {
  transition: opacity 0.3s, transform 0.3s;
}
.slide-down-enter,
.slide-down-leave-active {
  opacity: 0;
  transform: translateY(-15px);
}


